  position: absolute;
  right: 50px;
  top: 70px;
  font-size: 26px;
  transition: -webkit-transform ease-out .2s;
  transition: transform ease-out .2s;
  transition: transform ease-out .2s, -webkit-transform ease-out .2s;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}

