.progress{
  height: 5px;
  margin-bottom: 30px;
  box-shadow: none;
  background:#ececec;
  border-radius: 0;
  overflow: visible;
}

.progress-bar{
  position: relative;
  border-radius: 4px;
  background-color: var(--main-color);
  box-shadow: none;
  color: inherit;
  width: 20%;
}

.progress-bar span{
  position: absolute;
  top: -30px;
  right: 0px;
  color: #212121;
}



.progress-svg {
    display: block;
    margin: 0 auto;
    overflow: hidden;
    -webkit-transform: rotate(-90deg) rotateX(180deg);
    transform: rotate(-90deg) rotateX(180deg);
    width: 180px;
    height: 180px;
}
.progress-svg circle {
    stroke-dashoffset: 0;
    transition: stroke-dashoffset 0.8s cubic-bezier(.42,0,.58,1);
      stroke: #111;
      stroke-width: 5px;
}
.progress-svg .bar {
    stroke: #ececec;
    stroke-width: 6px;
}

.progress-svg circle {
    stroke: var(--main-color);
}
.progress-ring {
    position: relative;
    display: inline-block;
    margin: 40px 40px;
    -webkit-transform: translate3d(0px, 0px, 0px);
    transform: translate3d(0px, 0px, 0px);
}
.progress-ring:before {
    position: absolute;
    top: 47%;
    left: 50%;
    font-size: 24px;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    content: attr(data-circle-text);
    font-family: 'Poppins', sans-serif;
    color: #212121;
}
.progress-ring:after {
    position: absolute;
    top: 61%;
    left: 50%;
    font-size: 14px;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    content: attr(data-circle-percent) "%";
    font-family: 'Poppins', sans-serif;
    color: #212121;
}
.progress-ring-icon .progress-ring:before {
    position: absolute;
    top: 200px;
    left: 50%;
    font-size: 18px;
    line-height: 1.4;
    font-family: 'Poppins', sans-serif;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    content: attr(data-circle-text);
    color: #212121;
}
.progress-ring-icon .progress-ring:after {display:none;}
.progress-ring-icon i {
    position: absolute;
    top: 52%;
    left: 50%;
    font-size: 60px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}


